.testimonials__section{width:100%;height:100%;padding:120px 0 120px}.testimonials__title{text-align:center;position:relative;font-weight:500;margin-top:-50px;margin-bottom:60px;padding-bottom:20px;color:#fff}@media screen and (max-width: 411px){.testimonials__title{font-size:20px}}.testimonials__section h1::after{content:"";position:absolute;width:170px;height:3px;bottom:0;background:#fff;left:50%;transform:translateX(-50%)}.testimonials__section .card:before{content:"";position:absolute;top:0;left:-50%;width:100%;height:100%;transform:skewX(-5deg);background:rgba(255,255,255,.2)}.testimonials__section .card{position:relative;max-width:350px;height:auto;padding:50px 20px;background-color:#ff0;border-radius:10px;border:4px solid #fff;box-shadow:0 5px 15px rgba(0,0,0,.5);animation:animate 2s linear infinite;overflow:hidden}@media screen and (max-width: 411px){.testimonials__section .card{margin-bottom:30px}}.testimonials__section .card .image{position:relative;width:100px;height:100px;border-radius:50%;margin:20px auto;overflow:hidden;border:4px solid #fff;z-index:1}.img-responsive{max-width:120px;height:90px}.testimonials__section .card .details h2{position:relative;color:#fff;margin:0;padding:0;font-size:24px;z-index:1}.testimonials__section .card .details h2 span{font-size:18px}.testimonials__section .card .text{position:relative;color:#fff;font-size:16px;padding-top:50px;text-align:center}.testimonials__section .card .text:before{content:"ÔÑç";position:absolute;top:-30px;left:50%;transform:translateX(-50%);font-family:"Font Awesome 5 Free";font-weight:900;font-size:60px;color:rgba(255,255,255,.5)}.testimonials__section .col-sm-4:nth-child(1) .card{background:#ada43e url("../assets/images/sparkles.png")}.testimonials__section .col-sm-4:nth-child(2) .card{background:#e0841a url("../assets/images/sparkles.png")}.testimonials__section .col-sm-4:nth-child(3) .card{background:rgba(167,23,23,.527) url("../assets/images/sparkles.png")}@keyframes animate{0%{background-position:0 0}100%{background-position:0 -64px}}